import Vue from 'vue'
import VueRouter from 'vue-router'
import ProductsDetail from './components/Fashion/ProductsDetail.vue'
import Navigation from './components/Fashion/Navigation.vue'
import Product from './components/Fashion/Product.vue'
import ProductItem from './components/Fashion/ProductItem.vue'
import Introduce from './components/Fashion/Introduce.vue'




Vue.use(VueRouter)

export const router = new VueRouter({
    routes: [
        {path: '/Product/:id', name:'Product', component: Product},
        {path: '/ProductItem/:id', name:'ProductItem', component: ProductItem},
        {path: '/productsdetail/:id', component: ProductsDetail },
        {path: '/', component: Navigation },
        {path: '/Introduce', component: Introduce },

    ]
})