<template>
    <div class="product">
            <b-row class=" location_item" >
                
                    <p>Trang chủ / Sản phẩm / Thời trang nam / {{location}}</p>
            </b-row>
            
            <div class="adjustment">
                
                    <h3>{{location}}</h3>
                
                    <button>
                        <span>{{choosenadjust}}</span>
                        <i @click="adjustArr ^=true">
                            <fas icon="chevron-circle-down"/>
                            <ul v-show="adjustArr==true" class="adjusthidden">
                                <li @click="choosen">Thứ tự mặc định</li>
                                <li @click="choosenincre">Giá thấp đến cao </li>
                                <li @click="choosendecre">Giá cao đến thấp</li>
                            </ul>
                        </i>
                    </button>
                
            </div>
            <b-row >
                <b-col cols="6" md="4" lg="3" v-for="(item,index) in itemm" :key="index" class="product_item">
                    <router-link :to="'/Productsdetail/' + item.id">
                            <img :src="item.src" alt=""> 
                    </router-link>
                    <div class="iconadd">
                                <small>{{item.price}}</small>
                                <i><fas icon="cart-plus"/></i>
                    </div>
                    <router-link :to="'/Productsdetail/' + item.id">
                            <p>{{item.name}}</p>
                    </router-link>
                            
                    
                    
                </b-col>
                
                
            </b-row>
        
    </div>
</template>

<script>

export default {
    data(){
        return {
            adjustArr: false,
            choosenadjust:'Thứ tự mặc định',
        }
    },
    methods:{
        choosen(){
            this.choosenadjust = 'Thứ tự mặc định'
            this.$router.go();
        },
        choosendecre(){
            this.choosenadjust = 'Giá cao đến thấp'
            function compare(a, b) {
            if (a.price < b.price)
                return 1;
            if (a.price > b.price)
                return -1;
            return 0;
            }
            return this.itemm.sort(compare);
        },
        choosenincre(){
            this.choosenadjust = 'Giá thấp đến cao'

            function compare(a, b) {
            if (a.price < b.price)
                return -1;
            if (a.price > b.price)
                return 1;
            return 0;
            }

            return this.itemm.sort(compare);
        },
    },
    computed:{
        itemm(){
            let id = this.$route.params.id.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, '').replace(/\s/g, '').slice(0,-3);
            let filterArray = this.$store.state.cartItems.filter((item)=>{
                if(item.id.search(id)!=-1)
                return item
            })
            
            return filterArray
            
        },
        location(){
            return this.$route.params.id
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '@/assets/Scss/ProductPerson.scss' 
</style>