<template>
    <section>
        <b-row class=" location_item">
            <p>Trang chủ / Sản phẩm / Thời trang nam / {{item.name}}</p>
        </b-row>
        <b-row class="detail_item">
            <b-col class="image" cols="10" md="6">
                <img :src="item.src" alt="">
            </b-col>
            <b-col class="information_item" cols="12" md="6">
                <h5>{{item.name}}</h5>
                <p class="price">{{item.price}}</p>
                <div class="color">
                    <h6> Màu sắc :</h6>
                    <div>
                        <span>Xanh </span>
                        <span>Trắng </span>
                        <span>Xám </span>
                    </div>
                </div>
                <div class="size">
                    <h6> Size:</h6>
                    <div>
                        <span>S </span>
                        <span>M </span>
                        <span>L </span>
                        <span>XL </span>
                    </div>
                </div>
                <div class="quality">
                    <button @click="qualitydecre"><span>-</span></button>
                    <p>{{quality}}</p>
                    <button @click="quality++"><span>+</span></button>
                </div>
                <div class="addCart">
                    <button><span>Thêm vào giỏ hàng</span></button>
                </div>
                <div class="descreption">
                    <h6>Mô tả </h6>
                    <ul>
                        <li> Chất liệu: 100% COTTON</li>
                        <li>Form: Oversized</li>
                        <li> Màu: trắng, xám đậm, xanh nhạt</li>
                        <li>Size: S – M– L – XL</li>
                        <li>Sản phẩm đã có mặt ở toàn bộ các cửa hàng trên hệ thống</li>
                    </ul>
                    <h6>Hướng dẫn chọn size</h6>
                    <ul>
                        <li>Size M: 50-57kg / Cao 1m53 – 1m68</li>
                        <li>Size L: 58-64kg / Cao 1m57 – 1m70</li>
                        <li>Size XL: 65-70kg / Cao 1m66 – 1m76</li>
                        <li>Size XXL: 71-76kg / Cao 1m70 – 1m85.</li>
                        <li>Tùy mỗi người thích body hoặc vừa người thì tăng hoặc giảm 1 size, chỉ số trên là tương đối mặc</li>
                    </ul>
                </div>

            </b-col>
        </b-row>
        
        <b-row>
            <b-col cols="6" md="4" lg="3" v-for="(item,index) in itemnear" :key="index" class="product_item">
                    <router-link :to="'/Productsdetail/' + item.id">
                            <img :src="item.src" alt=""> 
                    </router-link>
                    <div class="iconadd">
                                <small>{{item.price}}</small>
                                <i><fas icon="cart-plus"/></i>
                    </div>
                    <router-link :to="'/Productsdetail/' + item.id">
                            <p>{{item.name}}</p>
                    </router-link>
                </b-col>
            
        </b-row>
        
    </section>
</template>

<script>

export default {
    
    data(){
        return{
            quality:0
        }
    },
    methods:{
        qualitydecre(){
            if(this.quality>0){
                this.quality--;
            }
        },
    },
    computed:{
        item(){
            let id =this.$route.params.id;
            
            let filterArr = this.$store.state.cartItems.filter((item)=>{
                    return item.id ==id;
                });
                if(filterArr.length>0) {
                    return filterArr[0];
                }
            return{}
        },
        itemnear(){
            
            let filterArrr = this.$store.state.cartItems.filter((item)=>{
                
                if(item.id.slice(0,-8)==this.item.id.slice(0,-8))
                return item
            });
                const a= Math.floor(Math.random() * 5);
                return filterArrr.slice(a,4+a);
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/Scss/ProductDetail.scss'

</style>